.container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    padding: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.64);
    z-index: 100;
  }
  
  .inner {
    height: fit-content;
    background-color: #fff;
    border-radius: 4px;
  }