.container {
    min-width: 700px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
  
  .inner {
    display: flex;
    gap: 30px;
  }
  
  .left {
    flex: 1;
  }
  
  .right {
    flex: 1.5;
  }
  .left,
  .right {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }
  
  .left .image {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .left .image img {
    height: 130px;
    width: 230px;
    cursor: pointer;
    object-fit: cover;
  }
  
  .left .image p {
    font-weight: 500;
    font-size: 0.875rem;
    letter-spacing: 1px;
  }
  
  .left .image p span {
    font-weight: bold;
    font-size: 0.875rem;
    letter-spacing: 1px;
  }
  
  .right .description,
  .description .inputs {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .right .description .top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .description .title {
    font-weight: 500;
    color: #252525;
    font-size: 0.9rem;
  }
  
  .description .link {
    color: #22c48b;
    font-weight: bold;
    font-size: 0.875rem;
    cursor: pointer;
  }
  
  .description .inputs .input {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .description .inputs .input > div {
    flex: 1;
  }
  .description .inputs .input svg {
    height: 18px;
    width: 18px;
    cursor: pointer;
    color: gray;
  }
  
  .footer {
    display: flex;
    gap: 20px;
    justify-content: flex-end;
    align-items: center;
  }
  
  .footer .cancel {
    font-size: 1rem;
    font-size: 500;
    color: gray;
    transition: 200ms;
    cursor: pointer;
  }
  .footer .cancel:hover {
    color: #000;
  }
  
  .error {
    font-size: 0.875rem;
    font-weight: bold;
    color: rgb(243, 80, 59);
    text-align: center;
    width: 100%;
  }