.App {
    min-height: 100vh;
    height: fit-content;
    width: 100%;
  }
  
  .spinner {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  button {
    border: none;
    outline: none;
    padding: 8px 16px;
    background-color: #c909bc;
    color: #fff;
    border-radius: 5px;
    width: fit-content;
    text-align: center;
    font-size: 1rem;
    cursor: pointer;
    transition: 200ms;
    display: flex;
    gap: 5px;
    align-items: center;
    justify-content: center;
  }
  
  button:hover {
    background-color: #790772;
  }
  
  button:active {
    transform: translateY(1px);
  }
  
  button:disabled {
    background-color: gray !important;
  }